/* // Page Header Module Stylesheet
// ======================================== */

/* Imports */
@import url(../settings/borders.module.css);
@import url(../settings/colors.module.css);
@import url(../settings/spacers.module.css);

/* Structure */
.header {
  background-color: var(--unity-t-white-base);
  -webkit-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.03);
          box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.03);
  left: 0;
  padding: var(--unity-spacer-16);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.header__left,
.header__right {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.header__left {
  float: left;
}

.header__left i,
.header__right i {
  margin-right: var(--unity-spacer-8);
}

.header__left p {
  font-size: var(--unity-font-16);
  font-weight: var(--unity-font-bold);
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.header__right {
  float: right;
}



