/* // Badge Component Stylesheet
// ======================================== */

/* Imports */
@import url(../settings/colors.module.css);
@import url(../settings/typography.module.css);

/* Structure */
.badge {
  border-radius: 2px;
  display: inline-block;
  padding: var(--unity-spacer-4);
  text-align: center;
}

.badge__text {
  color: var(--unity-t-white-base);
  font-size: var(--unity-font-10);
  font-weight: var(--unity-font-bold);
}

/* Types */
.primary {
  composes: primary from "../settings/alerts.module.css";
}

.success {
  composes: success from "../settings/alerts.module.css";
}

.caution {
  composes: caution from "../settings/alerts.module.css";
}

.danger {
  composes: danger from "../settings/alerts.module.css";
}



